import React from 'react';
//import ReactDOM from 'react-dom/client';
import ReactDOM from 'react-dom';
import './index.css';

//import AuthMenu from './Menu/AuthMenu';
import AuthMenu from './Menu/TestAuth';

import AuthSignIn from './AuthSignIn';

import SuperTokens, { SuperTokensWrapper } from 'supertokens-auth-react';

import { SuperTokensConfig } from './config';
import l from './logging';

console.log('info', 'SuperTokensConfig', SuperTokensConfig);
SuperTokens.init(SuperTokensConfig);

/*
    auth-signin id
    auth-menu  class

*/

// const root = ReactDOM.createRoot(document.getElementById("root") as HTMLElement);
// root.render(
//     <React.StrictMode>
//         <App />
//     </React.StrictMode>
// );

const signinEl = document.getElementById('auth-signin');
console.log('signinEl: ', signinEl);
if (document.getElementById('auth-signin')) {
  ReactDOM.render(
    <React.StrictMode>
      <SuperTokensWrapper>
        <AuthSignIn />
      </SuperTokensWrapper>
    </React.StrictMode>,
    document.getElementById('auth-signin')
  );
} else {
  console.log('prod mount: No id: auth-signin');
}

const listTargets = document.getElementsByClassName('auth-menu');
for (var listTarget of Array.from(listTargets)) {
  ReactDOM.render(
    <React.StrictMode>
      <SuperTokensWrapper>
        <AuthMenu />
      </SuperTokensWrapper>
    </React.StrictMode>,
    listTarget
  );
}
